@import './config';

.contact_headline{
    text-align: center;
    margin-top: 40px;
}

.come_in{
    width: 92.5%;
    margin: 20px auto;
    margin-bottom: 15px;
    text-align: center;
    max-width: 400px;
}

.locations_container{
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 20px;
    width: 95%;
    max-width: 1100px;
    margin: 0px auto;
    margin-bottom: 75px;
    
    @media screen and (min-width:650px){
        grid-template-columns: 1fr 1fr;
        
        .location{
            max-width: unset;
        }
    }

    .location{
        margin: 2.5% auto;
        background: white;
        border-radius: 2.5px;
        min-width: 250px;
        max-width: 550px;
        padding-bottom: 10px;
        width: 100%;
        box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.2);
        border-bottom: 3px solid $green;



        h2{
            text-align: center;
            font-size: clamp(20px, 20%, 40px);
            margin: 12.5px auto;
        }

        .image_container{
            position: relative;
            width: 100%;
            max-height: 600px;
    
            &::after{
                content: "";
                display: block;
                padding-bottom: 60%;
            }
    
            img{
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0; 
                width: 100%; 
                height: 100%; 
                object-position: top;
                background: lightgrey;
            }
        }

        h4{
            font-size: 15px;
            padding-left: 10px;
            margin-top: 7.5px;
        }   
    }
}