$green: rgb(3, 206, 3);
$lightgrey: rgba(243, 243, 243, 0.877);

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

@mixin flexCenter{
    display: flex;
    align-items: center;
    justify-content: center;
}

@mixin transition_ease{
    transition: all 0.5s ease-in-out;
}